  font-family: 'Roboto';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('truetype');
}

:root {
  --lightGrey: #b1b1b1;

  background-color: var(--lightGrey);
}

body {
  font-family: Roboto;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
}

.card {
  position:relative; /*Needed for absolute positioning of line */
  aspect-ratio: 1/1.75;
  /* width: 120px; */
  /* height: 170px; */
  border: 1px solid #ccc;
  border-radius: 25px;
  background-color: aliceblue;
  margin: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden; /* Ensures icons don't overflow if too many */
}

.card .icon-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* Adjust the number of columns as needed */
  gap: 4px; /* Adjust the gap between icons as needed */
}

.card i {
  font-size: 20px; /* Adjust icon size as needed */
}

.transparent-container {
  position: fixed;
  width: 68%; /* Adjust width as needed */
  top: 42%;
  left: 37%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.9); /* Adjust transparency as needed */
  padding: 20px;
  border-radius: 10px;
  color: white; /* Adjust text color as needed */
  z-index: 1000; /* Ensure the container is on top of other elements */
  display: flex;
  flex-direction: column; /* Align button container below content */
  align-items: center; /* Center button container horizontally */
}

.transparent-container #container {
  /* Add specific styles for your container content */
}

button-container {
  transform: translateX(-50%);
}

.close-button {
  background-color: green;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 30px;
  margin-right: 90px; /* Adjust space before the button as needed */
  margin-top: 45px;
  box-shadow: 4px 4px 4px rgb(252, 252, 252);
}

.draw-two-button {
  background-color: green;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 30px;
  margin: 20px;
  box-shadow: 4px 4px 4px rgb(252, 252, 252);
}

.draw-two-button:hover {
  color: rgb(15, 15, 15);
  box-shadow: 4px 4px 4px rgb(15, 15, 15);
}

/* unvisited link */
a:link {
  color: red;
}

/* visited link */
a:visited {
  color: green;
}

/* mouse over link */
a:hover {
  color: hotpink;
}

/* selected link */
a:active {
  color: blue;
